.ant-layout-sider .logo{
  position: sticky;
  top: 0;
}

.ant-layout-sider .ant-menu-root{
  position: sticky;
  top: 52px;
}

$anime-time: 8s;

$box-size: 200px;
$clip-distance: .05;
$clip-size: $box-size * (1 + $clip-distance * 2);
$path-width: 2px;

$main-color: #69ca62;
@keyframes clipMe {
  0% {clip: rect(0px, 2px, 0, 0px); }
  25% {clip: rect(0px, 2px, 301px, 0px); }
  50% {clip: rect(301px, 230px, 301px, 0px); }
  75% {clip: rect(0px, 230px, 301px, 230px); }
  100% {clip: rect(0px, 230px, 0px, 0px); }
}



